
import { defineComponent, ref, computed } from '@vue/composition-api';
import i18n from 'i18next';

export type orderBy = 'Most viewed' | 'Time' | 'Price';

const items = [
  {
    label: i18n.t('supplier-backend.preview.most_viewed'),
    name: 'click',
  },
  {
    label: i18n.t('supplier-backend.preview.time'),
    name: 'gmtStoreTime',
  },
  // {
  //   label: 'Price',
  //   name: 'price',
  // },
];

const orderSelector = defineComponent({
  props: {
    orderBy: {
      type: String as () => orderBy,
    },
    asc: {
      type: Boolean,
      default: true,
    },
  },
  render() {
    const self = this as InstanceType<typeof orderSelector>;
    return (
      <div class='flex-justify-flex-end order-group'>
        {items.map(item => {
          return (
            <div class={{
              'order-item': true,
              active: item.label === self.activeItemLabel,
              'flex-vertical-center': true,
            }}
            onclick={() => self.changeOrder(item)}>
              {item.label}
              {item.label !== self.activeItemLabel
                ? <i class='iconfont iconjiantoushang'></i>
                : self.asc
                  ? <i class='iconfont iconjiantoushang'></i>
                  : <i class='iconfont iconjiantouxia'></i>}
            </div>
          );
        })}
      </div>
    );
  },
  setup(props, { emit }) {
    const activeItemLabel = computed(() => items.find(item => item.name === props.orderBy)?.label);
    async function changeOrder(item: (typeof items)[0]) {
      if (activeItemLabel.value !== item.label) {
        emit('change', {
          order: item.name,
          asc: true,
        });
      } else {
        emit('change', {
          order: item.name,
          asc: !props.asc,
        });
      }
      return item;
    }
    return {
      activeItemLabel,

      changeOrder,
    };
  },
});
export default orderSelector;
